.jeneratedJoke__cover {
  width: 680px;
  min-height: 225px;
  background: #f8f8f8;
  border-radius: 20px;
  margin-bottom: 40px;
  padding: 40px;
  position: relative; }

.jeneratedJoke__like-btn {
  width: 20px;
  height: 17px;
  background-image: url(data:image/png;base64,);
  background-size: cover;
  background-repeat: no-repeat;
  position: absolute;
  right: 40px;
  background-color: transparent;
  border: none;
  outline: none; }

.jeneratedJoke__content {
  display: flex;
  height: 100%;
